import React from 'react';
import style from './style.module.css'
import MediaQuery from 'react-responsive'

const Greetings = () =>{
    return(
        <div className = {style.wrapper}>
            <MediaQuery maxDeviceWidth={1224}>
                <h1 className = {style.centerTextMobile}>Привет👋</h1>
                <h2 className = {style.centerTextMobile}>меня зовут Никита</h2>
            </MediaQuery>
            <MediaQuery minDeviceWidth={1224}>
                <h1 className = {style.centerText}>Привет👋</h1>
                <h2 className = {style.centerText}>меня зовут Никита</h2>
            </MediaQuery>
            <MediaQuery minDeviceWidth={1224}>
                <h2 className = {style.centerText}>Я создаю web-приложения. Ищу применение своим навыкам.</h2>
            </MediaQuery>
            <MediaQuery minDeviceWidth={1224}>
                <hr className = {style.horizontalLine}/>
            </MediaQuery>
        </div>
    )
}

export default Greetings;