import React from 'react';
import style from './style.module.css'


const Greetings = () =>{
    return(
        <div className = {style.wrapper}>
            <h1 className = {style.centerText}>Привет, меня зовут Никита 👋</h1>
            <h2 className = {style.centerText}>Я создаю web-приложения. Ищу применение своим навыкам.</h2>
            <hr className = {style.horizontalLine}/>
        </div>
    )
}

export default Greetings;